html{height:100vh}body{background-color:#222;color:#ddd;padding:0;margin:0;height:100vh}*{font-family:"Kumbh Sans", sans-serif}button{border:none;color:black;padding:0.5em;margin:0.2em;cursor:pointer;border-radius:0.2em;background-color:#999}button:hover{background-color:white}button.primary-button{background-color:#FF4DFF}button.primary-button:hover{background-color:#FF9EFF}input[type="text"]{padding:0.5em}a{color:white}a:visited{color:#ccc}a:hover{color:white}.leaflet-container{height:100%;background-color:#222}.leaflet-container .marker{background-color:rgba(0,0,0,0.75);border-radius:25%}.leaflet-container .marker.focused{animation:blink-animation 0.5s steps(5, start) infinite;-webkit-animation:blink-animation 0.5s steps(5, start) infinite}.leaflet-container .leaflet-control-attribution{color:#222}.leaflet-container .leaflet-control-attribution *{color:blue}@keyframes blink-animation{to{background-color:red}}@-webkit-keyframes blink-animation{to{background-color:red}}@media only screen and (max-width: 400px){.page-title{margin:0.33em 0}}
